
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'detail',
components: {
XHeader
},
data () {
return {
title: '',
content: '',
date: ''
}
},
created () {
this.title = this.$route.params.title
this.content = this.$route.params.content
this.date = this.$route.params.date
}
}



